.content {
  width: 300px;
  height: 300px;
  border: 1px solid black;
}
.content .content__list {
  width: 100px;
  height: 100px;
  border: 1px solid black;
}
.content .content__list .content__item {
  color: green;
}
.content .content__list .content__item:hover, .content .content__list .content__item:focus {
  color: red;
}